.AnswerItem{
  border: 1px solid white;
  border-radius: 5px;
  padding: 5px 10px;
  margin-bottom: 5px;
  cursor: pointer;
}
.AnswerItem:hover{
  background-color: rgba(255,255,255, .2);
  transition: background-color .3ms ease-in-out;
}
.AnswerItem.succes{
  background-color: green;
}
.AnswerItem.error{
  background-color: red;
}
