.FinishedQuiz{
  padding: 20px;
  color: white;
  border: solid 2px white;
  border-radius: 5px;
  box-sizing: border-box;
  margin: 0 10px;
}
.FinishedQuiz ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.FinishedQuiz ul li i{
  margin-left: 10px;
}

.succes{
  color: rgba(1, 255, 56, 0.7);
}
.error{
  color: rgba(252, 0, 0, 0.7);
}